import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Link } from 'react-router-dom';
import ReactSVG from '../../images/react_logo.svg';
import './GoldItem.scss';

@inject('gold')
@observer
class GoldItem extends Component {
  delayPageMove = pageName => {
    const {
      history: { push }
    } = this.props;
    const { common } = this.props.gold.root;
    common.loadingDelay(push, pageName);
  };

  render() {
    const { delayPageMove } = this;
    const { gold } = this.props;

    const buyGold = gold.buySise.map((info, index) => {
      if (index > 0) {
        return false;
      } else {
        return info.buyGold;
      }
    });
    const buyPrep = gold.buySise.map((info, index) => {
      if (index > 0) {
        return false;
      } else {
        return info.fluctuation === 'up' ? (
          <span key={index} className="up">
            (▲ {info.buyPrep})
          </span>
        ) : info.fluctuation === 'down' ? (
          <span key={index} className="down">
            (▼ {info.buyPrep})
          </span>
        ) : (
          <span key={index}>(- {info.buyPrep})</span>
        );
      }
    });
    const sellGold = gold.sellSise.map((info, index) => {
      if (index > 0) {
        return false;
      } else {
        return info.gold24;
      }
    });
    // console.log(gold.buySise[0]);

    return (
      <li className="gold">
        <div className="box">
          <p className="title">순금 시세</p>
          <p className="ts">Gold 24K 3.75g</p>
          <div className="table_box">
            <table>
              <colgroup>
                <col className="col" />
                <col />
              </colgroup>
              <tbody>
                <tr>
                  <th>살때</th>
                  <td>
                    {buyGold}원 {buyPrep}
                  </td>
                </tr>
                <tr>
                  <th>팔때</th>
                  <td>{sellGold}원</td>
                </tr>
              </tbody>
            </table>
          </div>

          <div className="round left">
            <img src={ReactSVG} alt="React Logo" />
          </div>
        </div>

        <Link
          to={{
            pathname: ''
          }}
          onClick={() => delayPageMove('gold')}
        >
          <div className="summary">
            <div className="line">
              <p className="t">한국금거래소 금시세표</p>
              <p className="u">ReactApp, 한국금거래소</p>
              <p className="s">2019.07.12</p>
            </div>
          </div>
        </Link>
      </li>
    );
  }
}

export default GoldItem;
