import React from 'react';
import ReactHighcharts from 'react-highcharts';

const GoldPageChart = ({ weekNotYearData, buyData, sellData }) => {
  let buyMin = Math.min.apply(null, buyData);
  let sellMin = Math.min.apply(null, sellData);
  let buyMax = Math.max.apply(null, buyData);
  let sellMax = Math.max.apply(null, sellData);

  // console.log(weekData);

  const config = {
    colors: ['#84b5df', '#e2b341'],
    chart: {
      zoomType: 'xy'
    },
    title: {
      text: ''
    },
    xAxis: [
      {
        categories: weekNotYearData,
        crosshair: true
      }
    ],
    yAxis: [
      {
        min: buyMin - (buyMin - sellMin),
        man: buyMax - (buyMax - sellMax),
        labels: {
          formatter: function() {
            return parseInt(this.value) + '원';
          }
        },
        title: {
          text: ''
        }
      }
    ],
    plotOptions: {
      series: {
        pointWidth: 25
      }
    },
    credits: {
      enabled: false
    },
    tooltip: {
      shared: true
    },
    legend: {
      align: 'right',
      verticalAlign: 'top',
      borderWidth: 0
    },
    series: [
      {
        name: '살때',
        type: 'column',
        data: buyData,
        tooltip: {
          valueSuffix: ' 원'
        }
      },
      {
        name: '팔때',
        type: 'spline',
        data: sellData,
        tooltip: {
          valueSuffix: '원'
        }
      }
    ]
  };

  return (
    <div className="chart">
      <ReactHighcharts config={config} />
    </div>
  );
};

export default GoldPageChart;
